	import{ mapState,mapMutations} from 'vuex'
export default {
        data() {
            return {
                userInfo:{},
				code:'',
				iv:'',
				// city:'',
				// province:'',
				encryptedData:'',
				
				
            };
        },
		computed: {
		           ...mapState(['hasLogin'])  
		       },  
        methods: {
			...mapMutations(['logIn'])  ,
			
            //第一授权获取用户信息===》按钮触发
            wxGetUserInfo() {
                let _this = this;
				uni.login({
						   provider: 'weixin',
						   success: function(loginRes) {
								_this.code=loginRes.code;
								uni.getUserInfo({
											   provider: 'weixin',
											   lang:'zh_CN',
											   success: function(infoRes) {
				　　　　　　　　　　　　　　　　　　　　　　//获取用户信息后向调用信息更新方法
													 _this.iv=infoRes.iv;
									
													 // _this.city=infoRes.userInfo.city;
													 // _this.province=infoRes.userInfo.province;
													 _this.encryptedData=encodeURIComponent(infoRes.encryptedData);
													 console.log(infoRes)
													 _this.getOpenId();
											   },
											
										   });
				                   },
				               });
            },
			
			isLogin(){
				let _this = this;
				   _this.xd_request(_this.xdServerUrls.xd_weiXinLogin,"POST",
				   {
					   userName: _this.userInfo.nickName,
					   userHead: _this.userInfo.avatarUrl,
					   city:_this.userInfo.city,
					   province:_this.userInfo.province,
				   encryptedData:_this.encryptedData,
				   iv:_this.iv,
				   code:_this.code,
				   },
				   {'content-type': 'application/x-www-form-urlencoded'} 
				   
				       ).then(res=>{
						   console.log(res.obj.id)
					
						   if(res.resultCode == 0){
							   console.log(_this.userInfo)
							   _this.logIn(_this.userInfo);
							   try{
								   uni.setStorageSync('token',res.obj.token);
								   uni.setStorageSync('id',res.obj.id);
							   }catch(e){
								   console.log(Error)
							   };
							  
							   uni.switchTab({
							       url: '../index/index'
							   });
						   }
					 
				   	}).catch(Error=>{
				   		console.log(Error)
				   	})
				   
				    
				},
			
			getOpenId(){
				 let _this = this;
				 let co='';
				 uni.login({
						   provider: 'weixin',
						   success: function(loginRes) {
								 co=loginRes.code;
								console.log(co)
								_this.xd_request_post(_this.xdServerUrls.xd_decodeUserInfo,
								{
								code:co,
								encryptedData:_this.encryptedData,
								iv:_this.iv,							
								}, false ).then(res=>{
									console.log(res)
									_this.userInfo=res.userInfo;
									wx.getSetting({
									       success(res) {
										   console.log(res)
											   if(res.authSetting["scope.userInfo"]){
											   
													_this.isLogin();
												
											   }
									       },
									       fail() {
									        
									       }
									   })
									// try{
									// 	   uni.setStorageSync('openId',res.userInfo.openId);
									// 	   uni.setStorageSync('unionId',res.userInfo.unionId)
									// }catch(e){
									// 	console.log(e)
									// };
								}
								).catch(Error=>{
               		console.log(Error)
               	})
								}
								})
				
					}
		
        },
        onLoad() {
        }
    }
